.container.contact__container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;
}

.contact__options {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

.contact__option {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact__option:hover {
    background: #324750;
    border-color: var(--color-primary-variant);
}

.contact__option-icon {
    font-size: 1.5rem;
    margin-bottom: .5rem;
}

.contact__option a {
    margin-top: .7rem;
    display: inline-block;
    font-size: .8rem;
}

/* === Text Form ==== */
form {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

input,
textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: .5rem;
    background: var(--color-bg-variant);
    border: 1px solid var(--color-primary-variant); /* Missing color value */
    resize: none;
    color: var(--color-white);
}

/* ===== Media Queries ======= */
@media screen and (max-width: 1024px) {
    .container.contact__container {
        grid-template-columns: 1fr;
        gap: 2rem;
    }
}

/* ====== Media Queries Small ==== */
@media screen and (max-width: 600px) {
    .container.contact__container {
        width: var(--container-width-sm); /* Missing variable or width value */
    }
}
